.main-nav {
  position: relative;
  font-size: 18px;
  line-height: 40px;
  font-weight: 700;
  min-height: 56px;
}
.main-nav__control {
  padding-bottom: 15px;
}
.main-nav__control:hover {
  opacity: 0.6;
}
.main-nav__control:active {
  opacity: 0.3;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  right: auto;
  left: auto;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: -223px;
  left: 280px;
  width: 22px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle:focus {
  outline: none;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__toggle {
  position: relative;
  top: -7px;
  left: 277px;
  border: none;
  background-color: transparent;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.main-nav__item-link {
  display: block;
  padding: 8px 20px 6px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  color: #ffffff;
  border-bottom: 1px solid #678fa8;
  border-top: 1px solid #9ac2db;
}
.main-nav__item-link:hover {
  color: #ffffff;
  background-color: #669ec0;
  box-shadow: none;
}
.main-nav__item-link:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav__item:last-child {
    position: relative;
  }
  .main-nav__item:last-child::after {
    content: "";
    position: absolute;
    display: block;
    bottom: 0;
    width: 100%;
    height: 20px;
    background: linear-gradient(to top, #678fa8 0, transparent 5px);
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    margin: 0;
  }
  .main-nav__item-link {
    padding: 9px 16px 7px 20px;
    color: #000000;
    background-color: #ffffff;
    border: none;
  }
  .main-nav__item-link:hover {
    color: #81b3d2;
    background-color: #ffffff;
  }
  .main-nav__item-link:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: #ffffff;
  }
  .main-nav__item-link--main {
    color: #766357;
  }
  .main-nav__item-link--main:hover {
    color: #604e43;
  }
  .main-nav__item-link--main:active {
    opacity: 0.3;
  }
  .main-nav__item {
    width: 20%;
    font-size: 14px;
    background-color: #ffffff;
  }
  .main-nav__item:nth-child(3) {
    margin-left: auto;
  }
  .main-nav__control {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    justify-content: space-around;
  }
}
